@import url(https://fonts.googleapis.com/css?family=Noto-Serif:300,300i,400,400i,500,500i,700,700i,900,900i);
body {
  margin: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button:focus {outline:0;}

code {
  /*font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;*/
}

.container {
    position: fixed;
    top: 0;
    left: 0;
    bottom:0;
    right:0;
}

.contents {
    background: #FFFFFF;
    height: 100%;
    width: 30%;
    float: left;
    vertical-align: left; 
}

.display {
    background: #EBECF0;
    height: 100%;
    width: 70%;
    float: right;
    overflow: scroll;
}

.name-header {
    padding-left: 40px;
    padding-bottom: 40px;
    padding-top: 40px;
    font-size: 30px;
}

.tab-btns {
    background: #FFFFFF;
    padding: 5px;
    padding-right: 20px;
    padding-left: 40px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin: 6px;
    font-size: 17px;
    border: none;
    color: #3A3B3C;
    transition: all 0.5s;
    cursor: pointer;
}

.tab-btns:hover{
    font-size: 18px;
    text-decoration: none;
}

.tab-btns:hover span:after {
    text-decoration: none;
    font-size: 17px;
    opacity: 1;
    transition: all 0.5s;
}


.background {
    background: #FFFFFF;
    margin: 60px;
    height: 70%;
    min-width: 600px;
    max-width: 1000px;
}

.title {
    padding-right: 60px;
    padding-top: 60px;
    padding-bottom: 40px;
    margin-bottom: -20px;
    font-size: 33px;
    font-weight: 900;
    max-width: 600px;
    min-width: 500px;
    width: 50%;
    margin: 0 auto;
}

.body {
    padding-right: 60px;
    padding-bottom: 20px;
    font-size: 19px;
    font-size-adjust: 0.58;
    line-height: 1.3;
    max-width: 600px;
    min-width: 500px;
    width: 50%;
    margin: 0 auto;
}

.icon {
    /*padding-left: 150px;*/
    padding-top: 30px;
    width: 60px;
    height: 60px;
    transition: all 0.5s;
    cursor: pointer;
}

.icon-next {
    padding-left: 80px;
    padding-top: 30px;
    width: 60px;
    height: 60px;
    transition: all 0.5s;
    cursor: pointer;
}

.images {
    width: 50%;
    margin: 0 auto;
}

.icon-next:hover{
    width: 65px;
    height: 65px;
    text-decoration: none;
}

.icon-next:hover span:after {
    text-decoration: none;
    width: 60px;
    height: 60px;
    transition: all 0.5s;
}

.icon:hover{
    width: 65px;
    height: 65px;
    text-decoration: none;
}

.icon:hover span:after {
    text-decoration: none;
    width: 60px;
    height: 60px;
    transition: all 0.5s;
}

.background-projects {
    background: #FFFFFF;
    margin: 60px;
    height: 2790px;
    min-width: 600px;
    max-width: 1000px;
}

.img {
    margin-top: 30px;
/*    width: 200px;*/
    height: 200px;
    transition: all 0.5s;
    cursor: pointer;
    float: center;
    display: block;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 15px; 
}

.img:hover{
    height: 220px;
    text-decoration: none;
}

.img:hover span:after {
    text-decoration: none;
    height: 200px;
    transition: all 0.5s;
}

.project-title {
/*    padding-bottom: 10px;*/
    font-size: 22px;
    width: 100%;
    text-align: center;
    font-weight: 600;
    color: #3A3B3C;
}

.project-body {
    font-size: 18px;
    padding-left: 20px;
    padding-right: 20px;
}

.project-container {
    margin-top: 20px;
    border: 1px solid #EBECF0;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
}

.custom-hr {
    width: 90%;
    color: #3A3B3C; 
}

.links {
    padding-left: 20px;
    transition: all 0.5s;
    cursor: pointer;
    font-weight: 600;
/*    color: #7285A5; */
    color: #4682B4;
    text-decoration: underline;
}

.links:hover{
    text-decoration: none;
    color: #7285A5;
}

.links:hover span:after {
    text-decoration: underline;
    color: #4682B4;
    transition: all 0.5s;
}

.tags {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: -20px;
/*    width: 50%;*/
/*    margin-left: auto;*/
/*    margin-right: auto;*/
    margin-left: 10px;
    overflow: scroll;
    width: 90%;
}

.tag {
    padding-right: 10px;
    padding-left: 10px;
    margin-left: 10px;
    margin-right: 10px;
    text-align: center;
    border: 2px solid #4C516D;
    border-radius: 10px;
    background: #7285A5;
    color: white;
    font-size: 12px;
    font-weight: 600;
    height: 15px; 
}

.email-link {
    transition: all 0.5s;
    cursor: pointer;
    font-weight: 600;
/*    color: #7285A5; */
    color: #4682B4;
    text-decoration: underline;
    max-width: 600px;
    min-width: 500px;
    width: 50%;
    margin: 0 auto;
}

.email-link:hover{
    text-decoration: none;
    color: #7285A5;
}

.email-link:hover span:after {
    text-decoration: underline;
    color: #4682B4;
    transition: all 0.5s;
}


.experience {
    padding-right: 60px;
    padding-bottom: 40px;
    max-width: 600px;
    min-width: 500px;
    width: 50%;
    margin: 0 auto;
}

.experience-name {
    font-size: 22px;
    font-weight: 900;
/*    color: #4C516D; */
    color: #7285A5;
}

.experience-position {
    font-size: 17px;
    margin-top: -5px; 
}

.experience-dates {
    margin-top: -10px;
    font-style: italic;
}

.experience-body {
    padding-right: 60px;
    padding-bottom: 10px;
    font-size: 19px;
    font-size-adjust: 0.58;
    line-height: 1.3;
    max-width: 600px;
    min-width: 500px;
    width: 50%;
    margin: 0 auto;
}

